import React from 'react'
import PropTypes from 'prop-types'
import Chip from '@material-ui/core/Chip'
import classNames from 'classnames'
// import MenuIcon from '@material-ui/icons/Menu'
import { makeStyles } from '@material-ui/core/styles'
import { green } from '@material-ui/core/colors'

const useStyles = makeStyles(theme => ({
  root: {
    display: 'flex'
  },
  chipContainer: {
    flex: '1 1'
  },
  chip: {
    height: theme.spacing(2.5),
  },
  label: {
    padding: theme.spacing(0, 1, 0, 1)
  },
  ok: {
    color: 'white',
    background: green[500],
    border: `1px solid ${green[500]}`
  },
  bad: {
    color: theme.palette.error.main,
    background: 'transparent',
    border: `1px solid ${theme.palette.error.main}`
  }
}))

const PasswordHelper = ({ pword }) => {
  const classes = useStyles()
  const hasUpper = pword ? !!pword.match(/[A-Z]/) : false
  const hasLower = pword ? !!pword.match(/[a-z]/) : false
  const hasNum = pword ? !!pword.match(/[0-9]/) : false
  const hasSpecial = pword ? !!pword.match(/[!@#$%^&*(),.?'":;{}\[\]\-_=+|/\\`~<>]/) : false
  const enoughChars = pword ? pword.length >= 8 : false
  return (
    <div className={classes.root}>
      <SpecialChip ok={enoughChars} label='8+' />
      <SpecialChip ok={hasUpper} label='A-Z' />
      <SpecialChip ok={hasLower} label='a-z' />
      <SpecialChip ok={hasNum} label='0-9' />
      <SpecialChip ok={hasSpecial} label='!@#$%^&*,.?' />
    </div>
  )
}

PasswordHelper.propTypes = {
  pword: PropTypes.string
}

export default PasswordHelper


const SpecialChip = ({ ok, ...props }) => {
  const classes = useStyles()

  return (
    <div className={classes.chipContainer}>
      <Chip 
      classes={{
        root: classNames(classes.chip, ok ? classes.ok : classes.bad),
        label: classes.label
      }}
      {...props}
      />
    </div>
  )
}

SpecialChip.propTypes = {
  ok: PropTypes.bool
}